import React, { useState, useMemo } from 'react'
import styled from 'styled-components'
import Dropdown from 'components/Dropdown'
import Flex from 'components/Flex'
import Box from 'components/Box'
import Text from 'components/Text'
import TabButton, { TAB_BUTTON_TYPE } from '../LeftFilter/Tabs/TabButton'

const Wrapper = styled(Flex)`
  position: relative;
  max-width: 1400px;
  margin: 0 auto;
  margin-top: 30px;
  align-items: center;
  flex-direction: column;

  @media (max-width: 1000px) {
    max-width: 90%;
  }
  @media (max-width: 576px) {
    max-width: 100%;
  }
`

const Title = styled(Text)`
  // // min-width: 126px;
  position: relative;
  font-style: normal;
  font-weight: 500;
  font-size: 1.8rem;
  color: #e0e0e0;
`
const ValueText = styled(Title)`
  font-weight: bold;
`

const BG = styled(Flex)`
  justify-content: center;
  align-items: center;
  position: relative;
  height: 70px;
  clip-path: polygon(17px 0, 100% 0, calc(100% - 18px) 100%, 0% 100%, 0% 34px);
  transition: all 0.3s ease-out;
  background: linear-gradient(180deg, #b596dc 0%, #3d296a 50%);
  &::before {
    content: '';
    display: block;
    position: absolute;
    top: 2px;
    left: 2px;
    height: calc(100% - 4px);
    width: calc(100% - 4px);
    clip-path: polygon(16.5px 0, 99.6% 0, calc(100% - 18px) 100%, 0 100%, 0 33.5px);
    background: #4c3886;
  }

  &:hover {
    opacity: 1;
    cursor: pointer;
  }

  &:active {
    opacity: 1;
  }
`

const VolumeIndicator = () => {
  const VOLUME_INDICATOR_OPTIONS = useMemo(() => {
    return [
      {
        symbol: 'Last 24H',
      },
      {
        symbol: 'Last 7D',
      },
    ]
  }, [])
  const [volumeUnit, setVolumeUnit] = useState(VOLUME_INDICATOR_OPTIONS[0])

  const [data, setData] = useState([
    { title: 'Items', value: '777/13035' },
    { title: 'Volume', value: '$1,134,567,890' },
    { title: 'Floor Price', value: '~$1,200' },
    { title: 'Owners', value: '10,000' },
  ])

  return (
    <Wrapper>
      <Box width="137px">
        <Dropdown
          position="left"
          options={VOLUME_INDICATOR_OPTIONS}
          value={volumeUnit}
          onHandleChange={(value) => {
            setVolumeUnit(value)
          }}
        />
      </Box>
      <Flex style={{ flexWrap: 'wrap', marginTop: '20px', justifyContent: 'center', marginBottom: '80px' }}>
        {data.map((el) => {
          return (
            <BG margin="10px 2px" width="270px">
              <Flex style={{ flexDirection: 'column', alignItems: 'center' }}>
                <Title>{el.title}</Title>
                <ValueText mt="6px">{el.value}</ValueText>
              </Flex>
            </BG>
          )
        })}
      </Flex>
    </Wrapper>
  )
}

export default VolumeIndicator
